<template>
  <div class="hello">    
    <Header></Header>
    <mt-loadmore 
    class="loadmore"
    :top-method="loadTop" 
    :bottom-method="loadBottom" 
     ref="loadmore">
     <div class="box"></div>
      <ul class="content" v-for="(item, index) in response" :key="index">
        <li id="linkto" @click="linkto(item.id)">{{item.title}}</li>
        <img :src='item.images[0]' >
      </ul>
    </mt-loadmore>

  </div>
</template>

<script>
import Vue from 'vue'
import {Loadmore ,Header, Button} from 'mint-ui'
import header from './header'
Vue.component(Loadmore.name, Loadmore);
Vue.component(Button.name, Button)
Vue.component(Header.name, Header)
export default {
  name: 'HelloWorld',
  data () {
    return {
      response: [],
      time: 1
    }
  },
  components:{
    Header: header
  },
  mounted() {
    this.getData();
    this.loadBottom();
  },
  methods: {
    getData(){
      this.$http({
                method: 'get',
                url:'https://zhihu-daily.leanapp.cn/api/v1/last-stories',          
            }).then(res=>{
                this.response = this.response.concat(res.data.STORIES.stories)
                console.log(res)
            })
    },
    linkto(id){
      this.$router.push({path:'/details/'+id})
    },
    getBeforeData(){
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth()+1;
      var day = date.getDate();
      console.log(date.getDate())
      if(month<9){
        month = '0' + month; 
      }
      if(day <9){
        day = '0' + day;
      }
      var before = year+''+month+day - this.time;
        this.$http({
                method: 'get',
                url:'https://zhihu-daily.leanapp.cn/api/v1/before-stories/'+before,          
            }).then(res=>{
                this.response = this.response.concat(res.data.STORIES.stories);
                this.time ++;
          }) 
    },
    loadTop(){
      this.response = [];
      this.getData();
      this.getBeforeData();
      this.$refs.loadmore.onTopLoaded();
    },
    loadBottom(){
      this.getBeforeData();
      // this.allLoaded = true;// 若数据已全部获取完毕        
      // this.$refs.loadmore.onBottomLoaded();
    },

  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.box{
  width: 100%;
  height: 4rem;
  background: pink;
}
.loadmore{
  font-size: 0.35rem;
  margin-top: 0.73rem;
}
#linkto{
  cursor: pointer;
}
#linkto:hover{
  color: orange;
}
.swipe{
  width: 100%;
  height: 4rem;;
  background: pink
}
li{
  font-size: 0.35rem;
}
.content{
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.3rem;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}
.content li{
  padding-top: 0.5rem;
}
.content >>>img{
  width: 2rem;
  height: 2rem;
}
</style>



