<template>
    <div class="lazyload">
        <Header></Header>
        <ul>
            <li  v-for="(item,index) in imgs" :key="index">
                <img v-lazy = item.url>
            </li>
        </ul>
    </div>
</template>
<script>
import { Lazyload, Header } from 'mint-ui'
import Vue from 'vue'
import header from './header'
Vue.use(Lazyload);
Vue.component(Header.name, Header);
Vue.component('Header', header);
export default {
    data(){
        return{
            imgs: []
        }
    },
    mounted() {
        this.$http({
            url: 'http://jsonplaceholder.typicode.com/photos',
            method: 'get'
        }).then(res=>{
            console.log(res);
           this.imgs = res.data
        })
    },
}
</script>
<style scoped>
.lazyload{
    font-size: 0.35rem;
}
ul{
    margin-top: 0.5rem;
}
img{
    width: 3rem;
    height: 3rem;
}
</style>



